<template>
  <div class="resumeEditor" :class="{htmlMode:enableHtml}" ref="container">
    <!-- <div v-if="enableHtml" v-html="result"></div>
    <pre v-else>{{result}}</pre> -->
     <div id="container">
        <div id="nose">
        </div>
        <div class="eye eye-left"></div>
        <div class="eye eye-right"></div>
        <div class="upperLip"></div>
        <div class="lowerLip-wrapper">
            <div class="lowerLip"></div>
        </div>
        <div class="face face-left"></div>
        <div class="face face-right"></div>
    </div>
  </div>
</template>

<script>
import marked from 'marked';
export default {
    props: ['markdown', 'enableHtml'],
    name: 'ResumeEditor',
    computed: {
        result: function() {
            return this.enableHtml ? marked(this.markdown) : this.markdown;
        }
    },
    methods: {
        goBottom: function() {
            this.$refs.container.scrollTop = 100000;
        },
        goTop: function() {
            this.$refs.container.scrollTop = 0;
        }
    }
};
</script>

<style scoped>
@media (max-width: 500px) {
    .resumeEditor {
    }
}
.htmlMode {
    animation: flip 2s;
}

@keyframes flip {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
</style>



